export function createPluncApp(name, id, configuration, registry, library) {
    let ready = false;
    // local state stored in closure
    const config = configuration;
    function getConfig() {
        return config;
    }
    function getLibrary() {
        return library;
    }
    function getRegistry() {
        return registry;
    }
    function getName() {
        return name;
    }
    function getId() {
        return id;
    }
    function nowReady() {
        ready = true;
    }
    function isReady() {
        return ready;
    }
    return {
        getConfig,
        getLibrary,
        getRegistry,
        getName,
        getId,
        nowReady,
        isReady,
    };
}
